@import "../../variables";

#mito .sketch-elements {
  font-family: $georgia-family;
}

.mito-hud {
  text-align: initial;
  background: $white;
  opacity: 0.95;
  box-shadow: 0 4px 6px 0 hsla(0, 0%, 0%, 0.2);
  border-bottom-right-radius: 2px;
  font-size: 12px;
  position: relative;
  right: 0;
  transition: right 2s;

  &.hidden {
    right: 100%;
  }

  .mito-hud-section {
    padding: 10px 10px;
  }

  .d-pad {
    // background: $gray1;
    // $size: 40px;
    display: grid;
    // grid-template: $size $size #{$size} / $size $size $size;
    grid-template: auto auto auto / auto auto auto;
    justify-items: stretch;
    align-items: stretch;
    justify-content: center;
    // color: $light-gray5;
  }
}

.mito-hud-button {
  border-radius: 2px;
  // border: 1px solid transparentize($gray3, 0.8);
  // margin: 5px;
  // padding: 5px;
  cursor: pointer;
  // box-shadow: 0 2px 6px 0 transparentize($black, 0.8);

  &:hover {
    .mito-hud-button-hotkey {
      top: -2px;
      left: -2px;
      box-shadow: 4px 4px 1px 0 $dark-gray1;
    }
  }

  &:active {
    .mito-hud-button-hotkey {
      top: 0;
      left: 0;
      box-shadow: none;
    }
  }

  .mito-hud-button-hotkey {
    font-family: "Courier New", Courier, monospace;
    font-weight: bold;
    // text-decoration: underline;
    font-size: 16px;
    border: 1px solid $dark-gray1;
    width: 30px;
    height: 30px;
    box-shadow: 2px 2px 1px 0 $dark-gray1;
    border-radius: 5px;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    position: relative;
    transition: left 0.2s, top 0.2s, box-shadow 0.2s;
    margin-right: 5px;
  }
}

.ui-popup {
  position: absolute;
  animation: popIn 0.2s both;

  @keyframes popIn {
    from {
      transform: translate(-50%, -50%) scale(0.75);
      opacity: 0;
    }

    to {
      transform: translate(-50%, -50%) scale(1);
      opactiy: 0.95;
    }
  }

  &.ui-popup-top {
    top: 25%;
    left: 50%;
  }

  &.ui-popup-bottom {
    bottom: 30%;
    left: 50%;

    .connector-line {
      width: 3px;
      height: calc(7.3vh - 100%);
      background: linear-gradient(to bottom, rgba(255, 255, 255, 0.5), white);
      z-index: -1;
      position: absolute;
      left: calc(50% - 1.5px);
      bottom: 100%;
    }
  }

  &.ui-popup-left {
    left: 33%;
    top: 50%;
  }

  .popup-title {
    border-top-left-radius: 2px;
    border-top-right-radius: 2px;
    background: white;
    padding: 10px;
  }

  .popup-content {
    border-radius: 2px;
    background: white;
    box-shadow: 0 4px 6px 0 hsla(0, 0%, 0%, 0.2);
  }

  .popup-row {
    display: flex;
    justify-content: center;
  }

  .popup-text {
    padding: 15px;
  }
}

.popup-autoplace {
  pointer-events: none;
  opacity: 0.5;

  .popup-content.popup-text {
    margin-top: 80%;
    padding: 5px;
  }

  .mito-hud-button {
    display: inline-block;
  }
}

.tutorial-content {
  font-size: 24px;
  padding: 20px;
}

.mito-inventory {
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  // left: 25%;
  // right: 25%;
  display: flex;
  flex-direction: column;
  opacity: 0.95;
  transition: transform 2s;
  border-top-left-radius: 2px;
  border-top-right-radius: 2px;
  border: 1px solid grey;
  background: rgba(255, 255, 255, 0.25);
  pointer-events: none;

  &.hidden {
    transform: translateX(-50%) translateY(170%);
  }

  .mito-inventory-container {
    z-index: 1;
    padding: 10px;
    background: hsl(111, 70%, 27%);
    border-bottom: 1px solid grey;
    margin-bottom: 10px;
  }

  .mito-inventory-indicator {
    font-weight: bold;
  }

  .mito-inventory-water {
    color: hsl(242, 81%, 42%);
  }

  .mito-inventory-sugar {
    color: hsl(41, 73%, 67%);
  }

  .mito-inventory-maxed {
    text-align: center;
    position: absolute;
    bottom: calc(100% - 100px);
    // background: $light-gray5;
    background: hsl(111, 70%, 27%);
    font-weight: bold;
    padding: 5px;
    border-top-left-radius: 2px;
    border-top-right-radius: 2px;
    align-self: center;
    transition: bottom 0.2s;
    color: $white;

    &.is-maxed {
      bottom: 100%;
    }
  }

  .mito-inventory-bar {
    height: 10px;
    width: 100%;
    display: flex;
    flex-direction: row;

    .mito-inventory-bar-water {
      background: hsl(242, 81%, 42%);
      transition: width 0.05s;
    }

    .mito-inventory-bar-sugar {
      background: hsl(41, 73%, 67%);
      transition: width 0.05s;
    }

    .mito-inventory-bar-empty {
      box-shadow: 0 5px 6px inset rgba(0, 0, 0, 0.25);
      transition: width 0.05s;
    }
  }
}

.mito-instructions {
  width: 100%;
  height: 100%;
  position: absolute;
  background: rgba(255, 255, 255, 0.95);
  z-index: 1;
  overflow: auto;
  text-align: initial;

  .mito-instructions-container {
    max-width: 700px;
    margin: auto;
    position: relative;
    padding-bottom: 10vh;
  }

  .esc {
    position: absolute;
    cursor: pointer;
    right: 0;
    padding: 10px;
    margin: 10px;
    color: $gray3;

    &:hover {
      box-shadow: 2px 2px 1px transparentize($black, 0.8);
    }
  }

  h1 {
    padding: 80px 0;
    font-size: 80px;
  }

  .play-button {
    text-align: center;
    padding: 40px;
    font-size: 30px;
    cursor: pointer;
    margin: 0 auto 10vh auto;
    border: 5px solid $black;

    &:hover {
      box-shadow: 10px 10px 5px transparentize($black, 0.7);
    }
  }

  h2 {
    padding-top: 30px;
    padding-bottom: 20px;
  }

  h1,
  h2 {
    text-align: center;
  }

  p {
    font-size: 16px;
    font-family: $arial-family;
    margin-top: 10px;
  }

  ol {
    -webkit-padding-start: 20px;
  }
}

.world-dom-components {
  z-index: 1;
  position: absolute;

  >* {
    pointer-events: none;
    position: absolute;
  }
}

.fruit-indicator {
  color: white;
  font-size: 12px;
  width: 100px;
  transform: translate(-50%, -50%) translateY(-30px);
}

.tutorial-build {
  animation: growIn 2s both;
  position: relative;
  height: 1em;
  overflow: hidden;

  @keyframes growIn {
    from {
      transform: scale(0.01, 0.01);
    }

    to {
      transform: scale(1, 1);
    }
  }

  &.tutorial-build-tissue {
    b {
      color: darkgreen;
    }
  }

  &.tutorial-build-root {
    b {
      color: rgb(183, 94, 36);
    }
  }

  &.tutorial-build-leaf {
    b {
      color: yellowgreen;
    }
  }
}