@keyframes opacifyAndSlideIn {
  from {
    opacity: 0;
    transform: translateX(-80px) scale(0.75); }
  to {
    opacity: 1;
    transform: translateX(0) scale(1); } }

@keyframes opacifyIn {
  from {
    opacity: 0;
    transform: scale(0.75); }
  to {
    opacity: 1;
    transform: scale(1); } }

.cell-bar {
  display: flex;
  flex-direction: row;
  margin: 10px;
  pointer-events: initial; }
  .cell-bar .mito-hud-button-hotkey {
    background: white;
    margin-top: 10px; }
  .cell-bar .cell-bar-item.selected .cell-bar-item-icon {
    transform: scale(1.25); }
  .cell-bar .cell-bar-item-icon {
    transform: scale(1);
    transition: transform 0.025s;
    width: 60px;
    height: 60px;
    border: 1px solid darkgrey;
    border-radius: 2px;
    margin: 0 5px;
    color: white;
    text-shadow: -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 0 #000;
    font-size: 12px;
    font-family: Arial, Helvetica Neue, Helvetica, sans-serif;
    cursor: pointer;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
    background-blend-mode: multiply;
    image-rendering: pixelated; }
